import { mapState } from "vuex";
import CryptoJS from "crypto-js";
import GoogleAutocomplete from "../components/GoogleAutocomplete.vue";
export default {
  data() {
    return {
      btnState: true,
      emailError: "",
      showEditForm: false,
      isCollapse: false,
      handleCollapse: null,
      editedUserInfo: {
        firstName: "",
        lastName: "",
        phoneNumber: "",
        username: "",
        password: "",
        address: "",
        emailAddress: "",
        birthday: "",
        gravatarEmail: ""
      }
    };
  },
  computed: {
    ...mapState({
      userInfo: state => state.userInfo
    }),
    // generate Gravatar URL
    gravatarUrl() {
      if (this.userInfo && this.userInfo.emailAddress) {
        return this.userInfo.avatar === null ? "http://akarana.oss-ap-southeast-1.aliyuncs.com/car1.jpg" : this.userInfo.avatar;
      }
      return "";
    }
  },
  components: {
    GoogleAutocomplete
  },
  methods: {
    handleAddressChange(event) {
      this.editedUserInfo.address = event.target.value;
    },
    validateEmail() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(this.editedUserInfo.emailAddress)) {
        this.emailError = "Please enter a valid email address.";
        console.log(this.emailError);
      } else {
        this.emailError = "";
      }
    },
    async save() {
      if (this.editedUserInfo.birthday instanceof Date) {
        this.editedUserInfo.birthday = this.editedUserInfo.birthday.toLocaleDateString('en-CA');
      }
      await this.$store.dispatch({
        type: "changeUserInfo",
        payload: this.editedUserInfo
      });
      this.btnState = true;
      this.setEditedUserInfo();
    },
    setEditedUserInfo() {
      this.editedUserInfo = {
        ...this.$store.state.userInfo
      };
    },
    updateGravatar() {
      this.gravatarEmail = this.editedUserInfo.emailAddress;
    }
  },
  created() {
    {
      console.log(localStorage.getItem("userInfo"));
      // ...
      this.gravatarEmail = this.userInfo.emailAddress;
    }
    this.setEditedUserInfo();
  }
};